<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-alert title=""
                type="info"
                center
                show-icon
                :closable="false">
      </el-alert>
      <!--  -->
      <el-steps :space="200"
                :active="activeIndex - 0"
                finish-status="success"
                align-center>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
        <el-step title=""></el-step>
      </el-steps>

      <!--  -->
      <el-form :model="addFormData"
               :rules="AddFormRules"
               ref="AddFormRef"
               label-width="100px"
               :label-position="'top'">
        <!--  -->
        <el-tabs v-model="activeIndex"
                 :tab-position="'left'"
                 :before-leave="tabLeave"
                 @tab-click="tabChange">
          <!--   -->
          <el-tab-pane label=""
                       name="0">
            <el-form-item label=""
                          prop="goods_name">
              <el-input v-model="addFormData.goods_name"></el-input>
            </el-form-item>
            <el-form-item label=""
                          prop="goods_price">
              <el-input v-model="addFormData.goods_price"
                        type="number"></el-input>
            </el-form-item>
            <el-form-item label=""
                          prop="goods_weight">
              <el-input v-model="addFormData.goods_weight"
                        type="number"></el-input>
            </el-form-item>
            <el-form-item label=""
                          prop="goods_number">
              <el-input v-model="addFormData.goods_number"
                        type="number"></el-input>
            </el-form-item>
            <el-form-item label=""
                          prop="goods_cate">
              <el-cascader v-model="addFormData.goods_cate"
                           :options="catelist"
                           :props="cateProps"
                           @change="handleChange"></el-cascader>
            </el-form-item>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane label=""
                       name="1">
            <el-form-item :label="item.attr_name"
                          v-for="item in manyTabData"
                          :key="item.attr_id">
              <el-checkbox-group v-model="item.attr_vals"
                                 @change="manyChange">
                <el-checkbox :label="cb"
                             v-for="(cb,i) in item.attr_vals"
                             :key="i"
                             border></el-checkbox>
              </el-checkbox-group>
            </el-form-item>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane label=""
                       name="2">
            <el-form-item v-for="item in this.onlyTabData"
                          :key="item.attr_id"
                          :label="item.attr_name">
              <el-input v-model="item.attr_vals"></el-input>
            </el-form-item>
          </el-tab-pane>
          <!--  -->
          <el-tab-pane label=""
                       name="3">
            <el-upload :action="this.uploadUrl"
                       :on-preview="handlePreview"
                       :on-remove="handleRemove"
                       list-type="picture"
                       :headers="this.headersObj"
                       :on-success="handleSuccess">
              <el-button size="small"
                         type="primary"></el-button>
            </el-upload>
          </el-tab-pane>
          <el-tab-pane label=""
                       name="4">
            <quill-editor ref="myQuillEditor"
                          v-model="addFormData.goods_introduce" />
            <el-button type="primary"
                       style="margin-top:15px"
                       @click="addGoods"></el-button>
          </el-tab-pane>
        </el-tabs>
      </el-form>
    </el-card>

    <!--  -->
    <el-dialog title=""
               :visible.sync="picViewDialogVisible"
               width="50%">
      <img :src="this.picViewPath"
           style="width: 100%">
    </el-dialog>
  </div>
</template>

<script>
import _ from 'lodash'
export default {
  data () {
    return {
      //  
      activeIndex: '0',
      // 
      addFormData: {
        goods_name: '',
        goods_price: 0,
        goods_weight: 0,
        goods_number: 0,
        goods_cat: [],
        pics: [],
        goods_introduce: '',
        attrs: []
      },
      // 
      AddFormRules: {
        goods_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_cate: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      catelist: [],
      //  props
      cateProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 
      manyTabData: [],
      // 
      onlyTabData: [],
      //  url 
      uploadUrl: 'http://127.0.0.1:8888/api/private/v1/upload',
      // 
      headersObj: {
        Authorization: window.sessionStorage.getItem('token')
      },
      // 
      picViewPath: '',
      // 
      picViewDialogVisible: false
    }
  },
  created () {
    this.getCateList()
  },
  methods: {
    // 
    async getCateList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) return this.$message.error('')
      this.catelist = res.data
      console.log(this.catelist)
    },
    //  
    handleChange () {
      if (this.addFormData.goods_cate.length !== 3) {
        this.addFormData.goods_cate = []
      }
      console.log(this.addFormData.goods_cate)
    },
    // tab 
    tabLeave (activeName, oldActiveName) {
      if (oldActiveName === '0' && this.addFormData.goods_cate.length !== 3) {
        this.$message.error('')
        return false
      }
    },
    // tab  
    async tabChange () {
      console.log(this.activeIndex)
      // 
      if (this.activeIndex === '1') {
        // console.log(this.cateId)
        const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
          params: {
            sel: 'many'
          }
        })
        if (res.meta.status !== 200) return this.$message.error('')
        res.data.forEach(item => {
          item.attr_vals = item.attr_vals.length === 0 ? [] : item.attr_vals.split(' ')
        })
        this.manyTabData = res.data
        console.log(this.manyTabData)
      }
      // 
      if (this.activeIndex === '2') {
        const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
          params: {
            sel: 'only'
          }
        })
        if (res.meta.status !== 200) return this.$message.error('')
        this.onlyTabData = res.data
        console.log(this.onlyTabData)
      }
    },
    // 
    handlePreview (val) {
      this.picViewPath = val.response.data.url
      console.log(this.picViewPath)
      this.picViewDialogVisible = true
    },
    // 
    handleRemove (val) {
      //   
      // this.addFormData.pics.findIndex(x)
      const tempPath = val.response.data.tmp_path
      const i = this.addFormData.pics.findIndex(item => {
        return item.pic === tempPath
      })
      this.addFormData.pics.splice(i, 1)
    },
    // 
    handleSuccess (val) {
      console.log(val)
      // 
      const picInfo = {
        pic: val.data.tmp_path
      }
      this.addFormData.pics.push(picInfo)
      console.log(this.addFormData)
    },
    // 
    addGoods () {
      // 
      this.$refs.AddFormRef.validate(async valid => {
        if (!valid) return this.$message.error('')
        // attr 
        // 
        this.manyTabData.forEach(item => {
          const manyInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals.join(' ')
          }
          this.addFormData.attrs.push(manyInfo)
        })
        // 
        this.onlyTabData.forEach(item => {
          const onlyInfo = {
            attr_id: item.attr_id,
            attr_value: item.attr_vals
          }
          this.addFormData.attrs.push(onlyInfo)
        })
        // console.log(this.addFormData)
        //  goods_cat    
        //  
        // 
        //  
        const formData = _.cloneDeep(this.addFormData)
        formData.goods_cat = formData.goods_cate.join(',')
        // console.log(formData)
        const { data: res } = await this.$http.post('goods', formData)
        if (res.meta.status !== 201) return this.$message.error('')
        this.$message.success('')
        this.$router.push('/goods')
        console.log(res)
      })
    },
    // many 
    manyChange () {
      console.log(this.manyTabData)
    }
  },
  computed: {
    //  id
    cateId () {
      if (this.addFormData.goods_cate.length === 3) {
        return this.addFormData.goods_cate[2]
      }
      return null
    }
  }
}
</script>

<style lang="less" scoped>
.el-checkbox {
    margin: 0 5px 0 0 !important;
}
</style>
