<template>
    <div class="login_container">
        <div class="login_box">
            <!--  -->
            <div class="avatar_box">
                <img src="../assets/logo.png" alt="" />
            </div>
            <!--  -->
            <el-form
                label-width="0px"
                class="login_form"
                :model="loginForm"
                :rules="loginFormRules"
                ref="LoginFormRef"
            >
                <!--  -->
                <el-form-item prop="username">
                    <el-input
                        prefix-icon="el-icon-user-solid"
                        v-model="loginForm.username"
                    ></el-input>
                </el-form-item>
                <!--  -->
                <el-form-item prop="password">
                    <el-input
                        prefix-icon="el-icon-lock"
                        v-model="loginForm.password"
                        type="password"
                    ></el-input>
                </el-form-item>
                <!--  -->
                <el-form-item class="btns">
                    <el-button
                        type="primary"
                        @click="submitForm('LoginFormRef')"
                        ></el-button
                    >
                    <el-button type="info" @click="resetForm('LoginFormRef')"
                        ></el-button
                    >
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            // 
            loginForm: {
                username: 'admin',
                password: '123456'
            },
            // 
            loginFormRules: {
                // 
                username: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        min: 3,
                        max: 10,
                        message: ' 3  10 ',
                        trigger: 'blur'
                    }
                ],
                password: [
                    {
                        required: true,
                        message: '',
                        trigger: 'blur'
                    },
                    {
                        min: 6,
                        max: 15,
                        message: ' 6  15 ',
                        trigger: 'blur'
                    }
                ]
            }
        }
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate(async valid => {
                if (!valid) return
                const { data: res } = await this.$http.post(
                    'login',
                    this.loginForm
                )
                if (res.meta.status !== 200) {
                    return this.$message.error('')
                }

                // 1. token  sessionStorage 
                // 1.1. API
                // 1.2. token  token sessionStorage
                this.$message.success('')
                window.sessionStorage.setItem('token', res.data.token)
                // 2.  /home
                this.$router.push('/home')
            })
        },
        resetForm(formName) {
            this.$refs[formName].resetFields()
        }
    }
}
</script>

