<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>

    <!--  -->
    <el-card>
      <!--  -->
      <el-alert title=""
                type="warning"
                :closable="false"
                show-icon>
      </el-alert>

      <!--  -->
      <el-row class="cat_opt">
        <el-col>
          <span></span>
          <!--  -->
          <el-cascader v-model="selectedCateKeys"
                       :options="cateList"
                       :props="selectCateProps"
                       @change="selectedCateChange"
                       size='medium'
                       clearable></el-cascader>
        </el-col>
      </el-row>

      <!-- tab  -->
      <el-tabs v-model="activeName"
               @tab-click="handleClick"
               class="eltabs">
        <el-tab-pane label=""
                     name="many">
          <!--  -->
          <el-button type="primary"
                     :disabled="isBtnDisabled"
                     @click="showAddDialog"></el-button>
          <!--  -->
          <el-table :data="this.manyTableData"
                    border
                    stripe>
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag closable
                        v-for="(item,i) in scope.row.attr_vals"
                        :key="i"
                        @close="removeTags(i,scope.row)">
                  {{item}}
                </el-tag>
                <el-input class="input-new-tag"
                          v-if="scope.row.inputVisible"
                          v-model="scope.row.inputValue"
                          ref="saveTagInput"
                          size="small"
                          @keyup.enter.native="handleInputConfirm(scope.row)"
                          @blur="handleInputConfirm(scope.row)">
                </el-input>
                <el-button v-else
                           class="button-new-tag"
                           size="small"
                           @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index">
            </el-table-column>
            <!--  -->
            <el-table-column prop="attr_name"
                             label="">
            </el-table-column>
            <!--  -->
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button type="primary"
                           size="mini"
                           icon="el-icon-edit"
                           @click="showEditDialog(scope.row)"></el-button>
                <el-button type="danger"
                           size="mini"
                           icon="el-icon-delete"
                           @click="deletParams(scope.row.attr_id)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
        <el-tab-pane label=""
                     name="only">
          <el-button type="primary"
                     :disabled="isBtnDisabled"
                     @click="showAddDialog"></el-button>
          <!--  -->
          <el-table :data="this.onlyTableData"
                    border
                    stripe>
            <!--  -->
            <el-table-column type="expand">
              <template slot-scope="scope">
                <el-tag closable
                        v-for="(item,i) in scope.row.attr_vals"
                        :key="i"
                        @close="removeTags(i,scope.row)">
                  {{item}}
                </el-tag>
                <el-input class="input-new-tag"
                          v-if="scope.row.inputVisible"
                          v-model="scope.row.inputValue"
                          ref="saveTagInput"
                          size="small"
                          @keyup.enter.native="handleInputConfirm(scope.row)"
                          @blur="handleInputConfirm(scope.row)">
                </el-input>
                <el-button v-else
                           class="button-new-tag"
                           size="small"
                           @click="showInput(scope.row)">+ New Tag</el-button>
              </template>
            </el-table-column>
            <!--  -->
            <el-table-column type="index">
            </el-table-column>
            <!--  -->
            <el-table-column prop="attr_name"
                             label="">
            </el-table-column>
            <!--  -->
            <el-table-column label="">
              <template slot-scope="scope">
                <el-button type="primary"
                           size="mini"
                           icon="el-icon-edit"
                           @click="showEditDialog(scope.row)"></el-button>
                <el-button type="danger"
                           size="mini"
                           icon="el-icon-delete"
                           @click="deletParams(scope.row.attr_id)"></el-button>
              </template>
            </el-table-column>
          </el-table>
        </el-tab-pane>
      </el-tabs>
      <!--  -->
      <el-dialog :title="''+titleText"
                 :visible.sync="addDialogVisible"
                 width="50%"
                 @close="addDialogClosed">
        <el-form ref="addFormRef"
                 :model="addFormData"
                 label-width="100px"
                 :rules="addFormRules">
          <el-form-item :label="titleText"
                        prop="attr_name">
            <el-input v-model="addFormData.attr_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer"
              class="dialog-footer">
          <el-button @click="addDialogVisible = false"> </el-button>
          <el-button type="primary"
                     @click="addParams"> </el-button>
        </span>
      </el-dialog>
      <!--  -->
      <el-dialog :title="''+titleText"
                 :visible.sync="editDialogVisible"
                 width="50%"
                 @close="editDialogClosed">
        <el-form ref="editFormRef"
                 :model="editFormData"
                 label-width="100px"
                 :rules="editFormRules">
          <el-form-item :label="titleText"
                        prop="attr_name">
            <el-input v-model="editFormData.attr_name"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer"
              class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary"
                     @click="editForm"> </el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      cateList: [],
      // 
      selectedCateKeys: [],
      //  props
      selectCateProps: {
        expandTrigger: 'hover',
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 
      activeName: 'many',
      // 

      // 
      manyTableData: [],
      // 
      onlyTableData: [],
      // 
      addDialogVisible: false,
      // 
      addFormData: {
        attr_name: ''
      },
      // 
      addFormRules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      editDialogVisible: false,
      // 
      editFormData: {
      },
      // 
      editFormRules: {
        attr_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      inputVisible: false,
      // 
      inputValue: '',
      // 
      paramsTagsData: []
    }
  },
  created () {
    this.getCateList()
  },
  methods: {
    // 
    async getCateList () {
      const { data: res } = await this.$http.get('categories')
      if (res.meta.status !== 200) return this.$message.error('')
      this.cateList = res.data
    },
    selectedCateChange () {
      this.getParamsData()
    },
    async getParamsData () {
      // 
      if (this.selectedCateKeys.length !== 3) {
        this.selectedCateKeys = []
        this.manyTableData = []
        this.onlyTableData = []
        return
      }
      const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes`, {
        params: {
          sel: this.activeName
        }
      })
      if (res.meta.status !== 200) return this.$message.error('')
      res.data.forEach(item => {
        item.attr_vals = item.attr_vals ? item.attr_vals.split(' ') : []
        item.inputVisible = false
        item.inputValue = ''
      })
      console.log(res.data)
      console.log(this.paramsTagsData)
      if (this.activeName === 'many') {
        this.manyTableData = res.data
      } else {
        this.onlyTableData = res.data
      }
    },
    // 
    handleClick () {
      this.getParamsData()
    },
    showAddDialog () {
      this.addDialogVisible = true
      console.log()
    },
    // 
    addDialogClosed () {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addParams () {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post(`categories/${this.cateId}/attributes`, {
          attr_name: this.addFormData.attr_name,
          attr_sel: this.activeName
        })
        if (res.meta.status !== 201) return this.$message.error('')
        this.$message.success('')
        this.addDialogVisible = false
        this.getParamsData()
      })
    },
    // 
    async showEditDialog (val) {
      const { data: res } = await this.$http.get(`categories/${this.cateId}/attributes/${val.attr_id}`, {
        params: {
          attr_sel: this.activeName
        }
      })
      if (res.meta.status !== 200) return this.$message.error('')
      this.editFormData = res.data
      this.editDialogVisible = true
      console.log(val)
    },
    // 
    editDialogClosed () {
      this.$refs.editFormRef.resetFields()
    },
    // 
    async editForm () {
      console.log(this.editFormData)
      const { data: res } = await this.$http.put(`categories/${this.editFormData.cat_id}/attributes/${this.editFormData.attr_id}`, {
        attr_name: this.editFormData.attr_name,
        attr_sel: this.activeName,
        attr_vals: this.editFormData.attr_vals
      })
      console.log(res)
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.getParamsData()
      this.editDialogVisible = false
    },
    //  
    async deletParams (id) {
      const confirmRes = await this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmRes !== 'confirm') return this.$message.info('')
      const { data: res } = await this.$http.delete(`categories/${this.cateId}/attributes/${id}`)
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.getParamsData()
    },
    //  
    async handleInputConfirm (row) {
      if (row.inputValue.trim().length === 0) {
        row.inputValue = ''
        row.inputVisible = false
        return
      }
      //  return  
      row.attr_vals.push(row.inputValue)
      this.saveTagsChange(row)
    },
    //  
    async saveTagsChange (row) {
      const { data: res } = await this.$http.put(`categories/${row.cat_id}/attributes/${row.attr_id}`, {
        attr_name: row.attr_name,
        attr_sel: row.attr_sel,
        attr_vals: row.attr_vals.join(' ')
      })
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      row.inputValue = ''
      row.inputVisible = false
      console.log(res)
    },
    // 
    async removeTags (i, row) {
      row.attr_vals.splice(i, 1)
      this.saveTagsChange(row)
    },
    // 
    showInput (row) {
      row.inputVisible = true
      // 
      // $nextTick  
      this.$nextTick(_ => {
        this.$refs.saveTagInput.$refs.input.focus()
      })
    }
  },
  computed: {
    isBtnDisabled () {
      if (this.selectedCateKeys.length !== 3) {
        return true
      }
      return false
    },
    cateId () {
      if (this.selectedCateKeys.length === 3) {
        return this.selectedCateKeys[2]
      }
      return null
    },
    titleText () {
      if (this.activeName === 'many') {
        return ''
      }
      return ''
    }
  }
}
</script>

<style lang="less" scoped>
.cat_opt {
    margin-top: 20px;
}
.eltabs {
    margin-top: 15px;
}
.el-tag + .el-tag {
    margin-left: 10px;
    margin-top: 10px;
}
.button-new-tag {
    margin-left: 10px;
    height: 32px;
    line-height: 30px;
    padding-top: 0;
    padding-bottom: 0;
}
.input-new-tag {
    width: 90px;
    margin-left: 10px;
    vertical-align: bottom;
}
</style>
