                         size="mini"
                         @click="showProgressDialog"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="queryInfo.pagenum"
                     :page-sizes="[5, 10, 15, 20]"
                     :page-size="queryInfo.pagesize"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="total">
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title=""
               :visible.sync="editAdressDialogVisible"
               width="50%"
               @close="closeAdressDialog">
      <el-form ref="editAdressFormRef"
               :model="editAdressFormData"
               label-width="100px"
               :rules="editAdressFormRules">
        <el-form-item label="/"
                      prop="adress1">
          <el-cascader v-model="editAdressFormData.adress1"
                       :options="cityData"
                       :props="{ expandTrigger: 'hover' }"></el-cascader>
        </el-form-item>
        <el-form-item label=""
                      prop="adress2">
          <el-input v-model="editAdressFormData.adress2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="editAdressDialogVisible = false"> </el-button>
        <el-button type="primary"
                   @click="editAdressDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title=""
               :visible.sync="progressDialogVisible"
               width="50%">
      <el-timeline>
        <el-timeline-item v-for="(activity, index) in progressData"
                          :key="index"
                          :timestamp="activity.time">
          {{activity.context}}
        </el-timeline-item>
      </el-timeline>
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="progressDialogVisible = false"> </el-button>
        <el-button type="primary"
                   @click="progressDialogVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import cityData from './citydata.js'
export default {

  data () {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // 
      total: 0,
      // 
      orderListData: [],
      // 
      editAdressDialogVisible: false,
      // 
      editAdressFormData: {
        adress1: [],
        adress2: ''
      },
      // 
      cityData,
      // 
      editAdressFormRules: {
        adress1: [
          { required: true, message: '/', trigger: 'blur' }
        ],
        adress2: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      progressDialogVisible: false,
      progressData: []
    }
  },
  created () {
    this.getOrderList()
  },
  methods: {
    // 
    async getOrderList () {
      const { data: res } = await this.$http.get('orders', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) return this.$message.error('')
      this.total = res.data.total
      this.orderListData = res.data.goods
    },
    // 
    handleSizeChange (val) {
      this.queryInfo.pagesize = val
      this.getOrderList()
    },
    // 
    handleCurrentChange (val) {
      this.queryInfo.pagenum = val
      this.getOrderList()
    },
    // 
    showEditAdressDialog () {
      this.editAdressDialogVisible = true
    },
    // 
    closeAdressDialog () {
      this.$refs.editAdressFormRef.resetFields()
    },
    showProgressDialog () {
      const res = {
        data: [{
          time: '2018-05-10 09:39:00',
          ftime: '2018-05-10 09:39:00',
          context: ',,',
          location: ''
        }, {
          time: '2018-05-10 08:23:00',
          ftime: '2018-05-10 08:23:00',
          context: '[]  95338',
          location: ''
        }, {
          time: '2018-05-10 07:32:00',
          ftime: '2018-05-10 07:32:00',
          context: ' []',
          location: ''
        }, {
          time: '2018-05-10 02:03:00',
          ftime: '2018-05-10 02:03:00',
          context: '[], []',
          location: ''
        }, {
          time: '2018-05-09 23:05:00',
          ftime: '2018-05-09 23:05:00',
          context: ' []',
          location: ''
        }, {
          time: '2018-05-09 21:21:00',
          ftime: '2018-05-09 21:21:00',
          context: '[], []',
          location: ''
        }, {
          time: '2018-05-09 13:07:00',
          ftime: '2018-05-09 13:07:00',
          context: ' ',
          location: ''
        }, {
          time: '2018-05-09 12:25:03',
          ftime: '2018-05-09 12:25:03',
          context: '',
          location: ''
        }, {
          time: '2018-05-09 12:22:24',
          ftime: '2018-05-09 12:22:24',
          context: 'HLA',
