        }, {
          time: '2018-05-08 21:36:04',
          ftime: '2018-05-08 21:36:04',
          context: '',
          location: ''
        }],
        meta: {
          status: 200,
          message: ''
        }
      }
      this.progressData = res.data
      console.log(this.progressData)
      this.progressDialogVisible = true
    }
