<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--   -->
      <el-row :gutter="20">
        <!--  -->
        <el-col :span="8">
          <el-input placeholder=""
                    v-model="queryInfo.query"
                    class="input-with-select"
                    clearable
                    @clear="getGoodsList">
            <el-button slot="append"
                       icon="el-icon-search"
                       @click="searchGoodsData"></el-button>
          </el-input>
        </el-col>
        <!--  -->
        <el-col :span="4">
          <el-button type="primary"
                     @click="goAddGoodsPage"></el-button>
        </el-col>
      </el-row>
      <!--  -->
      <el-table :data="goodsListData"
                border
                stripe
                style="width: 100%">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="goods_name"
                         label="">
        </el-table-column>
        <el-table-column prop="goods_price"
                         label="()"
                         width="120px">
        </el-table-column>
        <el-table-column prop="goods_weight"
                         label=""
                         width="120px">
        </el-table-column>
        <el-table-column label=""
                         width="150px">
          <!--  -->
          <template slot-scope="scope">
            {{ scope.row.add_time | dateFormat}}
          </template>
        </el-table-column>
        <el-table-column label=""
                         width="300px">
          <template slot-scope="scope">
            <el-button type="primary"
                       icon="el-icon-edit"
                       size="mini"
                       @click="showEditGoodsDialog(scope.row.goods_id)"></el-button>
            <el-button type="danger"
                       icon="el-icon-delete"
                       size="mini"
                       @click="removeGoodsById(scope.row.goods_id)"></el-button>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="this.queryInfo.pagenum"
                     :page-sizes="[5, 10, 15, 20]"
                     :page-size="this.queryInfo.pagesize"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="this.total">
      </el-pagination>
    </el-card>

    <!--  -->
    <el-dialog title=""
               :visible.sync="editGoodsDialogVisible"
               width="50%">
      <el-form ref="editFormRef"
               :model="editFormData"
               label-width="100px"
               :rules="editFormRules">
        <el-form-item label=""
                      prop="goods_name">
          <el-input v-model="editFormData.goods_name"></el-input>
        </el-form-item>
        <el-form-item label=""
                      prop="goods_price">
          <el-input v-model="editFormData.goods_price"
                    type="number"></el-input>
        </el-form-item>
        <el-form-item label=""
                      prop="goods_number">
          <el-input v-model="editFormData.goods_number"
                    type="number"></el-input>
        </el-form-item>
        <el-form-item label=""
                      prop="goods_weight">
          <el-input v-model="editFormData.goods_weight"
                    type="number"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="editGoodsDialogVisible = false"> </el-button>
        <el-button type="primary"
                   @click="editGoods"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      // 
      goodsListData: [],
      // 
      total: 0,
      // 
      editGoodsDialogVisible: false,
      // 
      editFormData: {
        goods_name: '',
        goods_price: 0,
        goods_number: 0,
        goods_weight: 0
      },
      // 
      editFormRules: {
        goods_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_price: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_weight: [
          { required: true, message: '', trigger: 'blur' }
        ],
        goods_number: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getGoodsList()
  },
  methods: {
    // 
    async getGoodsList () {
      const { data: res } = await this.$http.get('goods', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) return this.$message.error('')
      this.goodsListData = res.data.goods
      this.total = res.data.total
    },
    // 
    handleSizeChange (val) {
      this.queryInfo.pagesize = val
      this.getGoodsList()
    },
    // 
    handleCurrentChange (val) {
      this.queryInfo.pagenum = val
      this.getGoodsList()
    },
    // 
    searchGoodsData () {
      this.getGoodsList()
    },
    // id 
    async removeGoodsById (id) {
      const confirmRes = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      // console.log(confirmRes)
      if (confirmRes !== 'confirm') return this.$message.info('')
      const { data: res } = await this.$http.delete(`goods/${id}`)
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.getGoodsList()
    },
    //  
    goAddGoodsPage () {
      this.$router.push('goods/add')
    },
    // 
    async showEditGoodsDialog (id) {
      const { data: res } = await this.$http.get(`goods/${id}`)
      if (res.meta.status !== 200) return this.$message.error('')
      this.editFormData = {
        goods_name: res.data.goods_name,
        goods_price: res.data.goods_price,
        goods_number: res.data.goods_number,
        goods_weight: res.data.goods_weight,
        goods_id: res.data.goods_id
      }
      this.editGoodsDialogVisible = true
    },
    editGoods () {
      console.log(this.editFormData.goods_id)
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return this.$message.error('')
        const { data: res } = await this.$http.put(`goods/${this.editFormData.goods_id}`, {
          goods_name: this.editFormData.goods_name,
          goods_price: this.editFormData.goods_price,
          goods_number: this.editFormData.goods_number,
          goods_weight: this.editFormData.goods_weight,
          goods_cat: '1,3,6'
        })
        if (res.meta.status !== 200) return this.$message.error('')
        this.$message.success('')
        this.editGoodsDialogVisible = false
        this.getGoodsList()
      })
    }
  }
}
</script>

<style lang="less" scoped>
.input-with-select .el-input-group__prepend {
    background-color: #fff;
}
</style>
