<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-button type="primary"
                 @click="showAddRoleDialog"></el-button>
      <el-table :data="rolesList"
                stripe
                border
                row-key="id">
        <el-table-column type="expand">
          <template slot-scope="scope">
            <el-row v-for="(item1,i1) in scope.row.children"
                    :key="item1.id"
                    :class="['bdbottom', i1 === 0 ? 'bdtop' : '','vcenter']">
              <!--  -->
              <el-col :span="5">
                <el-tag closable
                        @close="removeRightById(scope.row,item1.id)">{{item1.authName}}</el-tag>
                <i class="el-icon-caret-right"></i>
              </el-col>
              <!--  -->
              <el-col :span="19">
                <el-row v-for="(item2, i2) in item1.children"
                        :key="item2.id"
                        :class="[i2 ===0 ? '' : 'bdtop','vcenter']">
                  <!--  -->
                  <el-col :span="6">
                    <el-tag type="success"
                            closable
                            @close="removeRightById(scope.row,item2.id)">{{item2.authName}}</el-tag>
                    <i class="el-icon-caret-right"></i>
                  </el-col>
                  <el-col :span="18">
                    <el-tag type="warning"
                            v-for="item3 in item2.children"
                            :key="item3.id"
                            closable
                            @close="removeRightById(scope.row,item3.id)">{{item3.authName}}</el-tag>
                  </el-col>
                </el-row>
              </el-col>
            </el-row>
          </template>
        </el-table-column>
        <el-table-column type="index">
        </el-table-column>
        <el-table-column prop="roleName"
                         label="">
        </el-table-column>
        <el-table-column prop="roleDesc"
                         label="">
        </el-table-column>
        <!--  -->
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="primary"
                       icon="el-icon-edit"
                       @click="editRoles(scope.row)"></el-button>
            <el-button type="danger"
                       icon="el-icon-delete"
                       @click="deletRole(scope.row.id)"></el-button>
            <el-button type="warning"
                       icon="el-icon-setting"
                       @click="showSetRightDialog(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!--  -->
    <el-dialog title=""
               :visible.sync="setRightDialogVisible"
               width="50%"
               @close="setRightDialogClosed">
      <el-tree :data="rightList"
               :props="treeProps"
               show-checkbox
               default-expand-all
               node-key="id"
               :default-checked-keys="defKeys"
               ref="treeRef"></el-tree>
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="setRightDialogVisible = false"> </el-button>
        <el-button type="primary"
                   @click="allotRights"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title=""
               :visible.sync="editRoleDialogVisible"
               width="50%">
      <el-form ref="editRoleFormRef"
               :model="editRoleFormData"
               label-width="100px"
               :rules="editRoleFormRules">
        <el-form-item label=""
                      prop="roleName">
          <el-input v-model="editRoleFormData.roleName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="editRoleFormData.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="editRoleDialogVisible = false"> </el-button>
        <el-button type="primary"
                   @click="cptEdit"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title=""
               :visible.sync="addRoleDialogVisible"
               width="50%">
      <el-form ref="addRoleFormRef"
               :model="addRoleFormData"
               label-width="100px"
               :rules="addRoleRules">
        <el-form-item label=""
                      prop="roleName">
          <el-input v-model="addRoleFormData.roleName"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="addRoleFormData.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="addRoleDialogVisible = false"> </el-button>
        <el-button type="primary"
                   @click="addRole"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  created () {
    this.getRolesList()
  },
  data () {
    return {
      rolesList: [],
      setRightDialogVisible: false,
      rightList: [],
      treeProps: {
        children: 'children',
        label: 'authName'
      },
      //  id
      defKeys: [],
      //  id
      roleId: '',
      // 
      editRoleDialogVisible: false,
      // 
      editRoleFormData: {
        roleName: '',
        roleDesc: '',
        id: 0
      },
      // 
      editRoleFormRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      addRoleDialogVisible: false,
      // 
      addRoleFormData: {
        roleName: '',
        roleDesc: ''
      },
      addRoleRules: {
        roleName: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    // 
    async getRolesList () {
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) return this.$message.error('')
      this.rolesList = res.data
      console.log(this.rolesList)
    },
    // 
    async removeRightById (role, rightId) {
      const confirmRes = await this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmRes !== 'confirm') return this.$message.info('')
      const { data: res } = await this.$http.delete(`roles/${role.id}/rights/${rightId}`)
      if (res.meta.status !== 200) return this.$message.error('')
      //  
      role.children = res.data
    },
    async showSetRightDialog (role) {
      //  list 
      const { data: res } = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) return this.$message.error('')
      this.rightList = res.data

      //  id
      this.getLeafKeys(role, this.defKeys)
      this.setRightDialogVisible = true
      this.roleId = role.id
    },

    // id  defKeys 
    getLeafKeys (node, arr) {
      //   children  
      if (!node.children) {
        return arr.push(node.id)
      }
      node.children.forEach(item => this.getLeafKeys(item, arr))
    },
    // 
    setRightDialogClosed () {
      this.defKeys = []
    },
    // 
    async allotRights () {
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys()
      ]
      const idStr = keys.join(',')
      const { data: res } = await this.$http.post(
        `roles/${this.roleId}/rights`,
        { rids: idStr }
      )
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('!')
      this.getRolesList()
      this.setRightDialogVisible = false
    },
    // 
    editRoles (row) {
      console.log(row)
      this.editRoleFormData.roleName = row.roleName
      this.editRoleFormData.roleDesc = row.roleDesc
      this.editRoleFormData.id = row.id
      this.editRoleDialogVisible = true
    },
    async cptEdit () {
      this.$refs.editRoleFormRef.validate(async valid => {
        if (!valid) return this.$message.error('')
        const { data: res } = await this.$http.put(`roles/${this.editRoleFormData.id}`, {
          roleName: this.editRoleFormData.roleName,
          roleDesc: this.editRoleFormData.roleDesc
        })
        if (res.meta.status !== 200) return this.$message.error('')
        this.$message.success('')
        this.editRoleDialogVisible = false
        this.getRolesList()
      })
    },
    // 
    async deletRole (id) {
      // console.log(id)
      const confirmRes = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      // console.log(confirmRes)
      if (confirmRes !== 'confirm') return this.$message.info('')
      const { data: res } = await this.$http.delete(`roles/${id}`)
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.getRolesList()
    },
    // 
    showAddRoleDialog () {
      this.addRoleDialogVisible = true
    },
    // 
    addRole () {
      this.$refs.addRoleFormRef.validate(async valid => {
        if (!valid) return this.$message.error('')
        const { data: res } = await this.$http.post('roles', {
          roleName: this.addRoleFormData.roleName,
          roleDesc: this.addRoleFormData.roleDesc
        })
        if (res.meta.status !== 201) return this.$message.error('')
        this.$message.success('')
        this.addRoleDialogVisible = false
        this.getRolesList()
      })
    }
  }
}
</script>

<style lang="less" scoped>
.el-tag {
    margin: 8px;
}
.bdbottom {
    border-bottom: 1px solid #eee;
}
.bdtop {
    border-top: 1px solid #eee;
}
.vcenter {
    display: flex;
    align-items: center;
}
</style>
