<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <el-row>
        <el-col>
          <el-button type="primary"
                     @click="showAddCateDialog"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <tree-table :data="catelist"
                  :columns="columns"
                  :selection-type="false"
                  :expand-type="false"
                  show-index
                  index-text="#"
                  border
                  :show-row-hover="false"
                  class="treeTable">
        <!--  -->
        <template slot="isok"
                  slot-scope="scope">
          <i class="el-icon-success"
             style="color: lightgreen"
             v-if="scope.row.cat_deleted === false"></i>
          <i class="el-icon-error"
             style="color: red"
             v-else></i>
        </template>
        <!--  -->
        <template slot-scope="scope"
                  slot="order">
          <el-tag v-if="scope.row.cat_level === 0"></el-tag>
          <el-tag v-else-if="scope.row.cat_level === 1"
                  type="success"></el-tag>
          <el-tag v-else
                  type="warning"></el-tag>
        </template>
        <!--  -->
        <template slot="opt"
                  slot-scope="scope">
          <el-button type="primary"
                     icon="el-icon-edit"
                     size="mini"
                     @click="showEditCateDialog(scope.row.cat_id)"></el-button>
          <el-button type="danger"
                     icon="el-icon-delete"
                     size="mini"
                     @click="deleteCate(scope.row.cat_id)"></el-button>
        </template>
      </tree-table>
      <!--  -->
      <el-pagination @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="queryInfo.pagenum"
                     :page-sizes="[3, 5, 10, 15]"
                     :page-size="queryInfo.pagesize"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="total">
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title=""
               :visible.sync="addCateDialogVisible"
               width="50%"
               @close="addCateDialogClosed">
      <el-form :model="addCateForm"
               label-width="100px"
               ref="addCateFormRef"
               :rules="addCateFormRules">
        <el-form-item label=""
                      prop="cat_name">
          <el-input v-model="addCateForm.cat_name"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-cascader expand-trigger="hover"
                       :options="parentCateList"
                       :props="cascaderProps"
                       v-model="selectedKeys"
                       @change="parentCateChanged"
                       clearable
                       change-on-select>
          </el-cascader>
        </el-form-item>
      </el-form>
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="addCateDialogVisible = false"> </el-button>
        <el-button type="primary"
                   @click="addCate"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title=""
               :visible.sync="editCatedialogVisible"
               width="50%"
               @close="closeEditCateDialog">
      <el-form ref="editCateFormRef"
               :model="editCateForm"
               label-width="100px">
        <el-form-item label="">
          <el-input v-model="editCateForm.cat_name"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="editCatedialogVisible = false"> </el-button>
        <el-button type="primary"
                   @click="editCate"> </el-button>
      </span>
    </el-dialog>

  </div>
</template>
<script>
export default {
  created () {
    this.getCateList()
  },
  data () {
    return {
      // 
      catelist: [],
      // 
      total: 0,
      // 
      queryInfo: {
        pagenum: 1,
        pagesize: 5
      },
      // 
      columns: [
        {
          label: '',
          prop: 'cat_name'
        },
        {
          label: '',
          type: 'template',
          template: 'isok'
        },
        {
          label: '',
          type: 'template',
          template: 'order'
        },
        {
          label: '',
          type: 'template',
          template: 'opt'
        }
      ],
      // 
      addCateDialogVisible: false,
      // 
      addCateForm: {
        cat_pid: '',
        cat_name: '',
        cat_level: ''
      },
      // 
      addCateFormRules: {
        cat_name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      // 
      parentCateList: [],
      // 
      selectedKeys: [],
      //   props
      cascaderProps: {
        value: 'cat_id',
        label: 'cat_name',
        children: 'children'
      },
      // 
      editCatedialogVisible: false,
      // 
      editCateForm: {
        cat_name: '',
        cat_id: ''
      }
    }
  },
  methods: {
    async getCateList () {
      const { data: res } = await this.$http.get('categories', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) return this.$message.error('')
      this.catelist = res.data.result
      this.total = res.data.total
      console.log(this.catelist)
    },
    // 
    // 
    handleSizeChange (val) {
      this.queryInfo.pagesize = val
      this.getCateList()
    },
    // 
    handleCurrentChange (val) {
      this.queryInfo.pagenum = val
      this.getCateList()
    },
    // 
    showAddCateDialog () {
      // 
      this.getParentCateList()
      this.addCateDialogVisible = true
    },
    // 
    async getParentCateList () {
      const { data: res } = await this.$http.get('categories', {
        params: {
          type: 2
        }
      })
      if (res.meta.status !== 200) return this.$message.error('')
      // console.log(res.data)
      this.parentCateList = res.data
    },
    // 
    parentCateChanged () {
      if (this.selectedKeys.length > 0) {
        //  id 
        this.addCateForm.cat_pid = this.selectedKeys[this.selectedKeys.length - 1]
        // 
        this.addCateForm.cat_level = this.selectedKeys.length
      } else {
        //  Id
        this.addCateForm.cat_pid = 0
        // 
        this.addCateForm.cat_level = 0
      }
    },
    // 
    async addCate () {
      console.log(this.addCateForm)
      this.$refs.addCateFormRef.validate(async valide => {
        if (!valide) return
        const { data: res } = await this.$http.post('categories', this.addCateForm)
        if (res.meta.status !== 201) return this.$message.error('')
        this.$message.success('')
        this.getCateList()
        this.addCateDialogVisible = false
      })
    },
    // 
    addCateDialogClosed () {
      this.$refs.addCateFormRef.resetFields()
      this.selectedKeys = []
      this.addCateForm.cat_level = 0
      this.addCateForm.cat_pid = 0
    },
    /*  */
    // 
    async showEditCateDialog (id) {
      const { data: res } = await this.$http.get('categories/' + id)
      if (res.meta.status !== 200) return this.$message.error('')
      this.editCateForm.cat_name = res.data.cat_name
      this.editCateForm.cat_id = res.data.cat_id
      this.editCatedialogVisible = true
    },
    //  id 
    async editCate () {
      const { data: res } = await this.$http.put('categories/' + this.editCateForm.cat_id, {
        cat_name: this.editCateForm.cat_name
      })
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.getCateList()
      this.editCatedialogVisible = false
    },
    //  
    closeEditCateDialog () {
      this.$refs.editCateFormRef.resetFields()
      this.editCateForm = {
        cat_name: '',
        cat_id: ''
      }
    },
    // /*  */
    async deleteCate (id) {
      const confirmRes = await this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmRes !== 'confirm') return this.$message.info('')
      const { data: res } = await this.$http.delete('categories/' + id)
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.getCateList()
    }
  }
}
</script>

<style lang="less" scoped>
.treeTable {
    margin-top: 15px;
}
.el-cascader {
    width: 100%;
}
</style>
