<template>
  <div>
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-card>
      <!--  -->
      <el-row :gutter="20">
        <el-col :span="9">
          <el-input placeholder=""
                    v-model="queryInfo.query"
                    clearable
                    @clear="getUserList">
            <el-button slot="append"
                       icon="el-icon-search"
                       @click="getUserList"></el-button>
          </el-input>
        </el-col>
        <el-col :span="6">
          <el-button type="primary"
                     @click="dialogVisible = true"></el-button>
        </el-col>
      </el-row>

      <!--  -->
      <el-table :data="userlist"
                border
                stripe>
        <el-table-column type="index">
        </el-table-column>
        <el-table-column prop="username"
                         label="">
        </el-table-column>
        <el-table-column prop="email"
                         label="">
        </el-table-column>
        <el-table-column prop="mobile"
                         label="">
        </el-table-column>
        <el-table-column prop="role_name"
                         label="">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.mg_state"
                       @change="userStateChanged(scope.row)">
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column label=""
                         width='220px'>
          <template slot-scope="scope">
            <el-button type="primary"
                       icon="el-icon-edit"
                       @click="showEditDialog(scope.row.id)"></el-button>
            <el-button type="danger"
                       icon="el-icon-delete"
                       @click="removeUser(scope.row.id)"></el-button>
            <el-tooltip class="item"
                        effect="dark"
                        content=""
                        placement="top"
                        :enterable='false'>
              <el-button type="warning"
                         icon="el-icon-setting"
                         @click="showSetRoleDialog(scope.row)"></el-button>
            </el-tooltip>

          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination @size-change="handleSizeChange"
                     @current-change="handleCurrentChange"
                     :current-page="1"
                     :page-sizes="[1, 2, 5, 10]"
                     :page-size="5"
                     layout="total, sizes, prev, pager, next, jumper"
                     :total="total">
      </el-pagination>
    </el-card>
    <!--  -->
    <el-dialog title=""
               :visible.sync="dialogVisible"
               width="50%"
               @close="addDialogClosed">
      <el-form ref="addFormRef"
               label-width="70px"
               :rules="addFormRules"
               :model="addForm">
        <el-form-item label=""
                      prop="username">
          <el-input v-model="addForm.username"></el-input>
        </el-form-item>
        <el-form-item label=""
                      prop="password">
          <el-input v-model="addForm.password"></el-input>
        </el-form-item>
        <el-form-item label=""
                      prop="email">
          <el-input v-model="addForm.email"></el-input>
        </el-form-item>
        <el-form-item label=""
                      prop="mobile">
          <el-input v-model="addForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary"
                   @click="addUsers"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title=""
               :visible.sync="editDialogVisible"
               width="50%"
               @close="editDialogClosed">
      <el-form ref="editFormRef"
               label-width="70px"
               :rules="editFormRules"
               :model="editForm">
        <el-form-item label="">
          <el-input v-model="editForm.username"
                    disabled></el-input>
        </el-form-item>
        <el-form-item label=""
                      prop="email">
          <el-input v-model="editForm.email"></el-input>
        </el-form-item>
        <el-form-item label=""
                      prop="mobile">
          <el-input v-model="editForm.mobile"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer"
            class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary"
                   @click="editUserInfo"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title=""
               :visible.sync="setRoleDialogVisible"
               width="50%"
               @close="setRoleDialogClosed">
      <p> {{this.userInfo.username}}</p>
      <p> {{this.userInfo.role_name}}</p>
      <p> <el-select v-model="selectRoleId"
                   placeholder="">
          <el-option :label="item.roleName"
                     :value="item.id"
                     v-for="item in roleList"
                     :key="item.id"></el-option>
        </el-select>
      </p>

      <span slot="footer"
            class="dialog-footer">
        <el-button @click="setRoleDialogVisible = false"> </el-button>
        <el-button type="primary"
                   @click="saveRoleInfo"> </el-button>
      </span>
    </el-dialog>

  </div>
</template>

<script>
export default {
  data () {
    var checkEmail = (rule, value, callback) => {
      // 
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
      if (regEmail.test(value)) {
        return callback()
      }
      callback(new Error(''))
    }
    var checkMobile = (rule, value, callback) => {
      // 
      const regMobile = /^(0|86|17951)?(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      if (regMobile.test(value)) {
        return callback()
      }
      callback(new Error(''))
    }
    return {
      queryInfo: {
        query: '',
        // 
        pagenum: 1,
        // 
        pagesize: 5
      },
      userlist: [],
      total: 0,
      dialogVisible: false,
      // 
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: ''
      },
      // 
      addFormRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 3,
            max: 10,
            message: '3~10',
            trigger: 'blur'
          }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          {
            min: 5,
            max: 15,
            message: '5~15',
            trigger: 'blur'
          }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
      // 
      editFormRules: {
        email: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkEmail, trigger: 'blur' }
        ],
        mobile: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur' }
        ]
      },
      editDialogVisible: false,
      editForm: {},
      // 
      setRoleDialogVisible: false,
      // 
      userInfo: {},
      // 
      roleList: [],
      // ID
      selectRoleId: ''
    }
  },
  created () {
    this.getUserList()
  },
  methods: {
    // 
    async getUserList () {
      const { data: res } = await this.$http.get('users', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) return this.$message.error('')
      this.userlist = res.data.users
      this.total = res.data.total
      console.log(res)
    },
    // 
    handleSizeChange (val) {
      this.queryInfo.pagesize = val
      this.getUserList()
    },
    // 
    handleCurrentChange (val) {
      this.queryInfo.pagenum = val
      this.getUserList()
    },
    //   put 
    async userStateChanged (val) {
      console.log(val)
      const { data: res } = await this.$http.put(
        `users/${val.id}/state/${val.mg_state}`
      )
      if (res.meta.status !== 200) {
        val.mg_state = !val.mg_state
        return this.$message.error('')
      }
      this.$message.success('')
    },
    // 
    addDialogClosed () {
      this.$refs.addFormRef.resetFields()
    },
    // 
    addUsers () {
      this.$refs.addFormRef.validate(async valide => {
        if (!valide) return
        // 
        const { data: res } = await this.$http.post('users', this.addForm)
        // console.log(res)
        if (res.meta.status !== 201) {
          this.$message.error('')
        }
        this.$message.success('')
        // 
        this.dialogVisible = false
        // 
        this.getUserList()
      })
    },
    // 
    editDialogClosed () {
      this.$refs.editFormRef.resetFields()
    },
    // 
    async showEditDialog (id) {
      this.editDialogVisible = true
      //  id  id 
      const { data: res } = await this.$http.get('users/' + id)
      if (res.meta.status !== 200) return this.$message.error('res.meta.msg')
      this.editForm = res.data
    },
    // 
    editUserInfo () {
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.put('users/' + this.editForm.id,
          {
            email: this.editForm.email,
            mobile: this.editForm.mobile
          }
        )
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        // 
        this.editDialogVisible = false
        // 
        this.getUserList()
        // 
        this.$message.success('')
      })
    },
    // 
    async removeUser (id) {
      const confirmRes = await this.$confirm('', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err).then()
      // console.log(confirmRes)
      //   confirm
      //  .then   id 
      console.log(confirmRes)
      if (confirmRes !== 'confirm') return this.$message.info('')
      const { data: res } = await this.$http.delete('users/' + id)
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.getUserList()
    },
    async showSetRoleDialog (userInfo) {
      this.userInfo = userInfo
      // 
      const { data: res } = await this.$http.get('roles')
      if (res.meta.status !== 200) return this.$message.error('')
      this.roleList = res.data
      this.setRoleDialogVisible = true
      // console.log(userInfo)
    },
    //  
    async saveRoleInfo () {
      if (!this.selectRoleId) return this.$message.error('')
      const { data: res } = await this.$http.put(`users/${this.userInfo.id}/role`, {
        rid: this.selectRoleId
      })
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.getUserList()
      this.setRoleDialogVisible = false
    },
    // 
    setRoleDialogClosed () {
      // console.log(this.selectRoleId)
      this.selectRoleId = ''
      this.userInfo = {}
    }
  }
}
</script>

<style lang="less" scoped>
</style>
